.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__title {
  color: #34568b;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0 24px;
  display: flex;
}

.card__reviews-value {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--rating-4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body, .header_nav a {
  font-family: Roboto, sans-serif;
}

.header_top {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.header_section {
  justify-content: flex-end;
  margin: 0 50px;
}

.header_logo {
  align-items: center;
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: flex;
}

.header_nav {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header_nav a {
  color: #060b35;
  align-content: center;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}

.nav_item {
  text-transform: uppercase;
  margin-right: 20px;
  position: relative;
}

.nav_item--active:after {
  content: "";
  background-color: #007bff;
  border-radius: 4px;
  width: 34px;
  height: 4px;
  position: absolute;
  bottom: -25px;
  left: 0;
}

.is-active {
  color: #007bff;
  position: relative;
}

.header_nav a:hover {
  color: #007bff;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 45px;
  display: grid;
}

@media (width <= 488px) {
  main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1200px) {
  main {
    margin-left: 128px;
    margin-right: 128px;
  }
}
